import YouTube from 'react-youtube';
import "../index.css"
const VideoThumbnail: any = ({videoUrl}:{videoUrl: string}) => {
    const videoId=videoUrl?.split("=")[1]
    const openVideo = () => {
        window.open(videoUrl)
    }
  return (
    <div onClick={openVideo} className="col-4 videoThumbnailCont">
      <img className="videoThumbnail" src={`https://img.youtube.com/vi/${videoId}/0.jpg`} />
      <h4>Video description</h4>
    </div>
  );
};

export default VideoThumbnail;
